import React from "react"
import Style from "./style.module.scss"
import Image from "../Projects/ProjectImage"
import Resume from "../../../Rohit.pdf"

const About = ({ duration, anim, easing }) => {
    return (
        <div className="main__container" id="about">
            <div className="main__container__title">
                <div>
                    <h2
                        data-sal={anim}
                        data-sal-delay={duration}
                        data-sal-easing={easing}
                    >
                        Who am I?
                    </h2>
                </div>
                <div className={Style.about__container}>
                    <div className={Style.about__image}>
                        <div
                            className={Style.image__container}
                            data-sal="fade"
                            data-sal-delay={duration}
                            data-sal-easing={easing}
                        >
                            <Image filename="me.png" alt="Rohit" />
                        </div>
                    </div>
                    <div className={Style.about__text}>
                        <div>
                            <p
                                data-sal={anim}
                                data-sal-delay={duration}
                                data-sal-easing={easing}
                            >
                                My name is Rohit Chaudhari. I am currently
                                pursuing my bachelors in the stream of Computer
                                Science at College of Engineering, Pune. Since
                                joining the course, I have greatly enhanced my
                                programming skills by learning new technologies,
                                working on various projects and competing in
                                various competitions.
                            </p>
                            <p
                                data-sal={anim}
                                data-sal-delay={duration}
                                data-sal-easing={easing}
                            >
                                I enjoy creating things for the internet,
                                whether that be websites, applications,
                                extensions, or anything in between. I can adapt
                                to new technology within a short amout of time.
                            </p>
                            <p
                                data-sal={anim}
                                data-sal-delay={duration}
                                data-sal-easing={easing}
                            >
                                When I am not programming, my free time is spent
                                on cricket and football. I am a huge Manchester
                                United fan and I love to discuss football
                                tactics.
                            </p>
                        </div>
                        <div
                            data-sal={anim}
                            data-sal-delay={duration}
                            data-sal-easing={easing}
                            className={Style.resume__link}
                        >
                            <a
                                href={Resume}
                                download="Rohit Chaudhari Resume.pdf"
                            >
                                Download Resume
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About
